<template>
<div id="carousel">
  <hooper :settings="hooperSettings">
  <slide>
    <span class="label">
      <div style="position: relative; display:inline-block;">
        <img src="../assets/image1.jpeg">
        <div class="img-title2">
          <p>九份</p>
        </div>
        <div class="img-text2">
          <p>「千と千尋の神隠し」の舞台という噂</p>
        </div>
      </div>
    </span>
  </slide>
  <slide>
    <span class="label">
      <div style="position: relative; display:inline-block;">
      <img src="../assets/image3.jpeg">
      <div class="img-title">
          <p>猴硐猫村</p>
        </div>
        <div class="img-text">
          <p>世界6大猫スポットの1つ</p>
        </div>
      </div>
    </span>
  </slide>
  <slide>
    <span class="label">
      <div style="position: relative; display:inline-block;">
        <img src="../assets/image4.jpeg">
        <div class="img-title">
          <p>運炭橋</p>
        </div>
        <div class="img-text">
          <p>炭鉱跡地につながる橋</p>
        </div>
      </div>
    </span>
  </slide>
  <slide>
    <span class="label">
      <div style="position: relative; display:inline-block;">
      <img src="../assets/image5.jpeg">
      <div class="img-title2">
          <p>安平樹屋</p>
        </div>
        <div class="img-text2">
          <p>天空の城ラピュタの世界観</p>
        </div>
      </div>
      </span>
  </slide>
  <slide>
    <span class="label">
      <div style="position: relative; display:inline-block;">
      <img src="../assets/image6.jpeg">
      <div class="img-title2">
          <p>九份夜景</p>
        </div>
      </div>
    </span>
  </slide>
  <hooper-pagination slot="hooper-addons"></hooper-pagination>
</hooper>
</div>
</template>
<script>
import {
  Pagination as HooperPagination
} from 'hooper'
export default {
  components: {
    Hooper: window.Hooper.Hooper,
    Slide: window.Hooper.Slide,
    HooperPagination
  },
  data () {
    return {
      hooperSettings: {
        itemsToShow: 1,
        autoPlay: true,
        playSpeed: 3000,
        transition: 3000,
        hoverPause: false,
        infiniteScroll: true
      }
    }
  }
}
</script>
<style scoped>
#carousel{
  max-width: 80%;
  margin: 0 auto;
}
/*
@media screen and (max-width: 1028px) {
  .hooper{
    height: 600px;
  }
} */
.hooper-slide {
    background-color: #fff;
    padding: 0px;
    display: block;
    justify-content: center;
    align-items: center;
    color: #fff;
    border: none;
    font-size: 30px;
  }
.img-title{
  position: absolute;
  bottom: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
  padding: 30px;
  text-align: left;
  color: white;
  font-size: 40px;
  font-family: "serif";
}
.img-text{
  position: absolute;
  top: 0;
  right: 0;
  max-width: 100%;
  max-height: 100%;
  padding: 30px;
  text-align: left;
  color: white;
  font-size: 20px;
  font-family: "serif";
}
.img-title2{
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
  padding: 30px;
  text-align: left;
  color: white;
  font-size: 40px;
  font-family: "serif";
}
.img-text2{
  position: absolute;
  bottom: 0;
  right: 0;
  max-width: 100%;
  max-height: 100%;
  padding: 30px;
  text-align: left;
  color: white;
  font-size: 20px;
  font-family: "serif";
}
</style>



