<template>
<div>
  <header>
    <!-- ナビゲーションバー -->
    <Navbar />
  </header>
  <main>
   <div id="app">
      <router-view/>
    </div>
  </main>
  <footer>
    <p>Web Traning <span class = "club">&clubs;</span> From Y.Watanabe</p>
  </footer>

  <!-- 画面上に戻るボタン -->
  <ScrollBtn />
</div>
</template>

<script>
// Navbarの読み込み
import Navbar from './components/Navbar.vue'
import ScrollBtn from './components/ScrollTop.vue'

export default {
  components: {
    // コンポーネントはここにも追記
    Navbar,
    ScrollBtn
  },
  name: 'App'
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
footer {
  position: relative;
  width: 100%;
  height: 80px;
  background-color: #333333;
}
footer p {
  text-align: center;
  margin: 0;
  color: white;
  padding: 26px 0px;
  font-size: 12px;
}
.club {
  font-size: 18px;
  margin: 0 4px;
  color: rgb(4, 100, 55);
}
.txt-main{
  font-size: 15px;
  color: #333333;
}
</style>



