<script>
import { Bar } from 'vue-chartjs'
export default {
  extends: Bar,
  mounted () {
    this.renderChart(
      {
        labels: [
          '1月',
          '2月',
          '3月',
          '4月',
          '5月',
          '6月',
          '7月',
          '8月',
          '9月',
          '10月',
          '11月',
          '12月'
        ],
        datasets: [
          {
            label: '台北の平均気温',
            borderColor: 'rgba(60,141,188,0.8)',
            fill: false,
            type: 'line',
            lineTension: 0.3,
            data: [16.3, 16.5, 18.3, 22.0, 25.3, 27.8, 29.7, 29.2, 27.5, 24.5, 21.5, 18.0],
            yAxisID: 'y-axis-1'
          },
          {
            label: '名古屋の平均気温',
            borderColor: 'rgba(219,77,77,0.8)',
            fill: false,
            type: 'line',
            lineTension: 0.3,
            data: [5.6, 7.2, 10.1, 14.1, 20.4, 23.1, 25.9, 28.9, 26.7, 20.3, 13.4, 8.8, 17.0],
            yAxisID: 'y-axis-1'
          },
          {
            label: '台北の降水量',
            borderColor: 'rgba(54,162,235, 0.5)',
            borderWidth: 1,
            backgroundColor: 'rgba(54,162,235,0.2)',
            type: 'bar',
            data: [103.5, 180.0, 189.5, 198.3, 233.9, 322.6, 251.1, 349.1, 372.4, 163.6, 98.0, 72.0],
            yAxisID: 'y-axis-2'
          },
          {
            label: '名古屋の降水量',
            borderColor: 'rgba(237,133,133,0.5)',
            borderWidth: 1,
            backgroundColor: 'rgba(237,133,133,0.2)',
            type: 'bar',
            data: [14.5, 56.5, 80.0, 117.5, 146.0, 172.0, 283.5, 204.0, 39.0, 356.5, 18.0, 68.0],
            yAxisID: 'y-axis-2'
          }
        ]
      },
      {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          xAxes: [
            {
            }
          ],
          yAxes: [
            {
              id: 'y-axis-1',
              scaleLabel: {
                display: true,
                labelString: '平均気温 [度]',
                fontSize: 15
              },
              type: 'linear',
              position: 'left',
              ticks: {
                beginAtZero: true,
                min: 0,
                max: 30
              }
            },
            {
              id: 'y-axis-2',
              scaleLabel: {
                display: true,
                labelString: '総降水量 [mm]',
                fontSize: 15
              },
              type: 'linear',
              position: 'right',
              ticks: {
                beginAtZero: true,
                min: 0,
                max: 400
              }
            }
          ]
        }
      }
    )
  }
}
</script>



