<template>
<div>
  <div class = "L-tewelve">
    <div class = "row">
      <div class = "banner-wrapper-fixed">
        <div class = "foreground">
          <h1>HELLOW WORLD</h1>
        </div>
      </div>
    <div class = "wrapper"></div>
    </div>
  </div>
  <div class = "L-tewelve">
    <div class = "row">
    <div class = "section">
      <h1 class = "heading"><span style="border-bottom: dashed 2px #fc0388;">PROFILE</span></h1>
      <p>あ</p>
      <p>い</p>
      <p>う</p>
      <p>え</p>
      <p>お</p>
    </div>
    </div>
    </div>
    <div class = "L-tewelve">
    <div class = "row">
    <div class = "section2">
      <div v-inview:parent="'animate'">
        <div :animate-toggle="'fadeInLeft'">
          <h1 class = "heading"><span style="border-bottom: dashed 2px #87ceeb;">TOPICS</span></h1>
          <p class="txt-main" >先週末、台湾に行ってきました<br>そのときの写真を紹介します<br></p>
          <br>
          <br>
        </div>
        <div :animate-toggle="'fadeInRight'">
          <!-- カルーセル -->
          <Carousel />
        </div>
      </div>
    </div>
    </div>
    </div>
    <div class = "L-tewelve">
    <div class = "row">
    <div class = "section3">
      <div class = "L-four T-six S-tewelve">
        <br>
        <div v-inview:parent="'animate'">
          <div :animate-toggle="'fadeInLeft'">
        <h1 class = "heading"><span style="border-bottom: dashed 2px orange;">TAIWAN'S INFO</span></h1>
        <p class="txt-main">グラフ作成練習のため<br>
         台北と日本の気候を比較しました<br>
        </p>
        <br>
        </div>
        <br>
          <div :animate-toggle="'fadeInRight'">
            <div class="tile is-ancestor" style="padding: 20px;">
            <div class="tile is-4 is-vertical is-parent">
              <div class="tile is-child box">
                <p class="title">台北の位置</p>
                <div style="position: relative; display:inline-block;">
                  <img src='../assets/taiwan.png' class="img-map" />
                  <div class="txt-map"><p>★</p>
                  </div>
                </div>
              </div>
              <div class="tile is-child box">
                <p class="title">基本情報</p>
                <div style="text-align: left;">
                  <p>説明文が入る</p>
                  <p>説明文が入る</p>
                  <p>説明文が入る</p>
                  <p>説明文が入る</p>
                  <p>説明文が入る</p>
                </div>
              </div>
            </div>
            <div class="tile is-parent">
              <div class="tile is-child box">
                <p class="title">気候の比較 (2019年)</p>
                <BarChart />
                <br>
                <br>
                <p>日本の南側にあるので年間通して気温が高く、雨の日が多いです。</p>
                <br>
                <div style="text-align: right; font-size: 12px">
                  <p>※各種データの情報は気象庁ホームページから抜粋</p>
                  <p>https://www.jma.go.jp/jma/index.html</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    </div>
    </div>
  </div>
</div>
</template>
<script>
import Carousel from '../components/Carousel.vue'
import BarChart from '../components/BarChart.vue'
import About from './about.vue'

export default {
  components: {
    Carousel,
    About,
    BarChart
  },
  name: 'Top'
}
window.addEventListener('scroll', function () {
  parallax('banner-wrapper-fixed', 5, 0)
})
function parallax (elem, speed, number) {
  var target = document.getElementsByClassName(elem)[number]
  var scrollValue = window.pageYOffset
  if (scrollValue <= 700) {
    target.style.transform = 'translateY(' + (-scrollValue / speed) + 'px)'
  }
}
</script>
<style scoped>
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: sans-serif;
  color: #1e1e1e;
  background: white;
  height: 100%;
}
.L-tewelve {
  background-color: white;
}
img {
  display: block;
  max-width: 100%;
}
.wrapper {
  position: relative;
  height: 600px;
}
.banner-wrapper-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 600px;
  background-image: url('../assets/image2.jpeg');
  background-position: center;
  background-size: cover;
  transition: all 0.1s ease 0s;
}
.foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
}
.banner-wrapper-fixed .foreground h1 {
  text-align: center;
  color: white;
  margin-top: 250px;
  font-size: 50px;
  padding: 0px 30px;
}
.heading {
  padding: 20px 0px;
  font-size: 40px;
  margin: 0;
  text-align: center;
}
.section {
  position: relative;
  width: 100%;
  height: auto;
  background-color: white;
}
.section2 {
  position: relative;
  width: 100%;
  height: auto;
  background-color: #faebd7;
}
.section3 {
  position: relative;
  width: 100%;
  height: auto;
  background-color: #f5f5dc;
}
.img-map{
  width: 260px;
  height: 280px;
}
.txt-map{
  position: absolute;
  top: 15px;
  right: 45px;
  max-width: 100%;
  max-height: 100%;
  padding: 0px;
  text-align: left;
  color: yellow;
  font-size: 20px;
  font-family: "serif";
}
</style>



